.nav {
  position: fixed;
  top: 0;
  padding: 20px;
  width: 100%;
  height: 30px;
  z-index: 1;

  transition-timing-function: ease-in;
  transition: all 0.5s;
}

.nav-black {
  background-color: #111;
}

.nav-content {
  display: flex;
  justify-content: space-between;
}

.nav-logo {
  position: fixed;
  top: 20px;
  left: 0;
  width: 90px;
  padding-left: 20px;
}

.nav-avatar {
  position: fixed;
  right: 20px;
  width: 30px;
}
